<template>
    <div class="wapper">
        <div class="operStage">
            <el-button type="primary" @click="createInstance()"></el-button>
            <el-dropdown @command="handleCommand">
                <el-button type="primary">
                    &nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-caret-bottom el-icon--right"></i>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item :disabled="currentRow==null||currentRow.mounton!=''" command="mountPrompt"></el-dropdown-item>
                    <el-dropdown-item :disabled="currentRow==null||currentRow.mounton==''" command="unmountPrompt"></el-dropdown-item>
                    <el-dropdown-item :disabled="currentRow==null||currentRow.state=='Allocated'" command="backupPrompt"></el-dropdown-item>
                    <el-dropdown-item :disabled="currentRow==null||currentRow.mounton!=''" command="deletePrompt"></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
            <div class="table">
                <el-table
                        :data="tableData"
                        border
                        tooltip-effect="dark"
                        style="width: 100%"
                        @select-all="handleSelectAll"
                        @select="handleCurrentChange">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            prop="id"
                            label=""
                            width="120"
                            show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column
                            prop="name"
                            label=""
                            width="120"
                            show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column
                            prop="size"
                            label="(G)"
                            show-overflow-tooltip
                            show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label=""
                            show-overflow-tooltip
                            show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column
                            prop="created"
                            label=""
                            show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column
                            prop="mounton"
                            label="id"
                            show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column
                            prop="mounton"
                            label=""
                            show-overflow-tooltip
                            :formatter="format">
                    </el-table-column>
                </el-table>
            </div>

            <el-dialog title="" v-model="dialogVisible" size="small" :modal-append-to-body=false>
                <div style="display:flex;border-top:2px solid #000">
                    <div class="config">

                        <div class="confWapper">
                            <div style="width: 80px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                            <el-select v-model="zone" placeholder="">
                                <el-option
                                        v-for="item in zoneOptions"
                                        :label="item.name"
                                        :value="item.id">
                                </el-option>
                            </el-select>
                        </div>

                        <div class="confWapper">
                            <div style="width: 80px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                            <div style="width:217px"><el-input v-model="diskName" placeholder=""></el-input></div>
                        </div>

                        <div class="confWapper">
                            <div style="width: 80px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                            <el-select v-model="disk" placeholder="" @change="changePay">
                                <el-option
                                        v-for="item in diskOptions"
                                        :label="item.descs"
                                        :value="item.size">
                                </el-option>
                            </el-select>
                        </div>

                        <div class="confWapper">
                            <div style="width:60%;padding-left:13px">
                                <el-slider v-model="disk" show-input @change="changePay" v-if="silder==true">
                                </el-slider>
                            </div>
                        </div>
                    </div>

                    <div class="display">


                        <div v-if="value=='current'">
                            <p></p>
                        </div>
                        <div v-if="value=='month'">
                            <p></p>
                        </div>
                        <div v-if="value=='year'">
                            <p></p>
                        </div>
                        <div>
                            <label> : </label>
                            <el-select v-model="value" placeholder="" class="eselect" @change="changePay">
                                <el-option
                                        v-for="item in payOptions" :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </div>
                        <div>
                            <label> : </label>
                            <el-select v-model="timeValue" :disabled="value=='current'" placeholder="" class="eselect" @change="changePay">
                                <el-option
                                        v-for="item in timeOptions" :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>

                        </div>
                        <div>
                            <label> :  </label>{{money}}<span v-if="value=='current'">/</span>

                        </div>
                    </div>
                </div>


                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" :disabled="disk==null||diskName==''" @click="finish"></el-button>
                </span>
            </el-dialog>

            <el-dialog title="" v-model="mountPrompt" size="tiny" :modal-append-to-body=false>
                <div class="config">

                    <div class="confWapper">
                        <div style="width: 80px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                        <el-select v-model="mountVMid" placeholder="">
                            <el-option
                                    v-for="item in mountOptions"
                                    :label="item.name"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                    </div>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="startPrompt = false"> </el-button>
                    <el-button type="primary" :disabled='mountVMid==null' @click="handleMount()"> </el-button>
                </span>
            </el-dialog>
            <el-dialog title="" v-model="unmountPrompt" size="tiny" :modal-append-to-body=false>
                <span>?</span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="stopPrompt = false"> </el-button>
                    <el-button type="primary" @click="handleUnmount()"> </el-button>
                </span>
            </el-dialog>
            <el-dialog title="" v-model="backupPrompt" size="tiny" :modal-append-to-body=false>
                <span>?</span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="stopPrompt = false"> </el-button>
                    <el-button type="primary" @click="handleBackup()"> </el-button>
                </span>
            </el-dialog>
            <el-dialog title="" v-model="deletePrompt" size="tiny" :modal-append-to-body=false>
                <span>?</span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="backupPrompt = false"> </el-button>
                    <el-button type="primary" @click="handleDelete()"> </el-button>
                </span>
            </el-dialog>
        </div>
    </div>

</template>

<script>
    /*flow*/
    import data from '../host/data.js';
    import util from '../util/util.js';
    import {Message,Loading} from 'element-ui'
    import dateFormatter from '../util/dateFormatter.js'
    export default{
        data(arg:string):object{
            return {
                tableData: [],
                currentRow:null,
                dialogVisible:false,
                zoneOptions:null,
                zone:null,
                diskOptions:null,
                disk:null,
                diskName:'',
                payOptions:data.payOptions,
                value:'current',
                timeOptions:data.monthOptions,
                timeValue:'1',
                money:'0',
                silder:false,

                /*
                *   API
                */
                bandwidth:0,
                CPUNum:0,
                CPUCache:{cache:0},

                /*
                    
                 */
                mountPrompt:false,
                unmountPrompt:false,
                deletePrompt:false,
                backupPrompt:false,

                mountOptions:[],
                mountVMid:null
            }
        },
        created(){
            this.$http.get('Disk/listDisk.do').then(response => {
                if(response.ok==true&&response.status==200){
                    //console.log(response.body);
                    this.tableData = dateFormatter.dateFormat(response.body.listvolumesresponse.volume);
                }
            },response => {

            })
        },
        methods:{
            handleCurrentChange(select,row){
                if(select.length>1){
                    select.splice(0,1);
                }
                this.currentRow = row;
                if(select.length==0){
                    this.currentRow = null;
                }
            },
            handleSelectAll(select){
                select.splice(0,select.length);
                this.currentRow = null;
            },
            deleteDisk(){
                this.$http.get('Disk/deleteVolume.do?diskid='+this.currentRow.id).then(response => {
                    if(response.ok==true&&response.status==200){
                        //console.log(response.body);
                        this.tableData = dateFormatter.dateFormat(response.body.listvolumesresponse.volume);
                    }
                },response => {

                })
            },
            handleCommand(type){
                if(type=='mountPrompt'){
                    this.$http.get('information/listVirtualMachines.do').then(response=>{
                        if(response.ok==true&&response.status==200){
                            this.mountOptions = response.body.listvirtualmachinesresponse.virtualmachine;
                        }
                    })
                }
                this[type] = true;
            },
            handleMount(){
                this.mountPrompt = false;
                let loadingInstance = Loading.service({
                    text:'...'
                })
                this.$http.get('Disk/attachVolume.do?virtualmachineid='+this.mountVMid+'&diskid='+this.currentRow.id).then(response => {
                    if(response.ok==true&&response.status==200){
                        console.log(response.body);
                        if(response.body.status==1){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'success'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvolumesresponse.volume);
                        }else if(response.body.status==2){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvolumesresponse.volume);
                        }else{
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                        //util.queryStartJobResult(this,response.body.startvirtualmachineresponse.jobid,loadingInstance);
                    }else{
                        loadingInstance.close();
                        Message({
                            message:'',
                            type:'error'
                        });
                    }
                }, response => {
                    loadingInstance.close();
                    Message({
                        message:'',
                        type:'error'
                    })
                })
            },
            handleUnmount(){
                this.unmountPrompt = false;
                let loadingInstance = Loading.service({
                    text:'...'
                })
                this.$http.get('Disk/detachVolume.do?diskid='+this.currentRow.id+'&virtualmachineid='+this.currentRow.mounton).then(response => {
                    if(response.ok==true&&response.status==200){
                        console.log(response.body);
                        if(response.body.status==1){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'success'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvolumesresponse.volume);
                        }else if(response.body.status==2){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvolumesresponse.volume);
                        }else{
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                        //util.queryStartJobResult(this,response.body.startvirtualmachineresponse.jobid,loadingInstance);
                    }else{
                        loadingInstance.close();
                        Message({
                            message:'',
                            type:'error'
                        });
                    }
                }, response => {
                    loadingInstance.close();
                    Message({
                        message:'',
                        type:'error'
                    })
                })
            },
            handleBackup(){
                this.backupPrompt = false;
                let loadingInstance = Loading.service({
                    text:'...'
                })
                this.$http.get('Snapshot/createSnapshot.do?volumeid='+this.currentRow.id).then(response => {
                    if(response.ok==true&&response.status==200){
                        //console.log(response.body);
                        loadingInstance.close();
                        if(response.body.status==1){
                            Message({
                                message:'',
                                type:'success'
                            });
                        }else if(response.body.status==2){
                            Message({
                                message:'',
                                type:'error'
                            });

                        }else{
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                        //util.queryStartJobResult(this,response.body.startvirtualmachineresponse.jobid,loadingInstance);
                    }else{
                        loadingInstance.close();
                        Message({
                            message:'',
                            type:'error'
                        });
                    }
                }, response => {
                    loadingInstance.close();
                    Message({
                        message:'',
                        type:'error'
                    })
                })
            },
            handleDelete(){
                this.deletePrompt = false;
                let loadingInstance = Loading.service({
                    text:'...'
                })
                this.$http.get('Disk/deleteVolume.do?diskid='+this.currentRow.id).then(response => {
                    if(response.ok==true&&response.status==200){
                        console.log(response.body);
                        if(response.body.status==1){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'success'
                            });
                            if(response.body.listvolumesresponse.hasOwnProperty("volume")){
                                this.tableData = dateFormatter.dateFormat(response.body.listvolumesresponse.volume);
                            }else{
                                this.tableData = [];
                            }


                        }else if(response.body.status==2){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvolumesresponse.volume);
                        }else{
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                        //util.queryStartJobResult(this,response.body.startvirtualmachineresponse.jobid,loadingInstance);
                    }else{
                        loadingInstance.close();
                        Message({
                            message:'',
                            type:'error'
                        });
                    }
                }, response => {
                    loadingInstance.close();
                    Message({
                        message:'',
                        type:'error'
                    })
                })
            },
            createInstance(){
                this.dialogVisible = true;
                this.$http.get('information/zone.do').then(
                    response =>{
                        if(response.ok==true&&response.status==200){
                            this.zoneOptions = response.body.listzonesresponse.zone;
                            this.zone = response.body.listzonesresponse.zone[0].id;
                        }
                    }
                )
                this.$http.get('Disk/listDiskTemplate.do').then(
                    response =>{
                        if(response.ok==true&&response.status==200){
                            this.diskOptions = response.body;
                            //this.disk = response.body[0].size;
                        }
                    }
                )
            },
            changePay(value){
                if(value == 0){
                    this.silder = true;
                }
                this.timeOptions = value=='month'?data.monthOptions:
                    value=='year'?data.yearOptions:this.timeOptions;
                //this.money = Math.random()*1000;
                if(value=='month'||value=='year'){
                    this.timeValue = '1';
                }
                util.calculationPay(this);
            },
            finish(){
                this.dialogVisible = false;
                let loadingInstance = Loading.service({
                    text:'...'
                })
                this.$http.get('Disk/createVolume.do?size='+this.disk+'&zoneid='+this.zone+'&name='+this.diskName+'&value='+this.value+'&timevalue='+this.timeValue).then(
                    response => {
                        loadingInstance.close();
                        if(response.ok==true&&response.status==200){
                            if(response.body.status==1){
                                Message({
                                    message:'',
                                    type:'success'
                                });
                                this.tableData = dateFormatter.dateFormat(response.body.listvolumesresponse.volume);
                            }else{
                                Message({
                                    message:'',
                                    type:'error'
                                });
                            }

                        }else{
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                    },
                    response => {
                        loadingInstance.close();
                        Message({
                            message:'',
                            type:'error'
                        });
                    }
                )
            },
            format(row){
                return row.state=='Ready'?'':'';
            }
        }
    }
</script>

<style scoped>
    .table{
        padding-top:15px;
    }
    .confWapper{
        display:flex;
        padding: 10px 37px;
    }
    .config{
        width:70%;
        margin-top:10px;
    }
    .display{
        background-color: #eaeaea;
        width:30%;
    }
    .display span{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .display div{
        border-bottom:1px solid #dddddd;
    }
    .display label{
        display: inline-block;
        width: 70px;
        height: 50px;
        text-align: right;
        line-height: 50px;
        font-size: 14px;
    }
    .display p{
        padding: 10px 15px;
        color: red;
    }
    .eselect{
        width: 165px;
    }

</style>


