<template>
    <div class="wapper">
        <div class="flex" style="display:flex">
            <div class="information">
                <div style="height:100%">
                    <label></label>
                    <div class="information-wapper" style="display:flex;height:100%;justify-content:space-between">
                        <div style="width:60%;height:119px;padding-top:20px;background-color: #f5f9fa">
                            <label style="padding-left:10%"></label>
                            <h2 style="line-height: 50px;padding-left:25%;">0</h2>
                        </div>
                        <div style="width:35%;height:119px;padding-top:20px;background-color: #f5f9fa">
                            <label style="padding-left:10%"></label>
                            <h2 style="line-height: 50px;padding-left:25%;">0</h2>
                        </div>
                    </div>
                </div>
            </div>
            <div class="information">
                <div>
                    <label></label>
                    <div>
                        <p>0&nbsp;&nbsp;&nbsp;30</p>
                        <p>0&nbsp;&nbsp;&nbsp;</p>
                        <p>0&nbsp;&nbsp;&nbsp;</p>

                    </div>
                </div>
            </div>
            <div class="information">
                <div>
                    <label></label>
                    <div style="margin-top:23px;font-size:17px">
                        <span style="font-size:33px">{{balance}}</span> 
                    </div>
                    <div style="margin-top:28px;">
                        <router-link to="/recharge">&nbsp;&nbsp;&nbsp;&nbsp;</router-link> <a href='#/feelog' style="margin-left: 20px;color: red;cursor: pointer;"></a>
                    </div>
                </div>
            </div>
        </div>
        <div class="operStage">
            <div class="display">
                <label></label>
            </div>
            <div style="display:flex">
                <div class="item">
                    <div class="item-icon" style="background-image:url('/YRCloud/resource/img/host.png');background-size: 55px 53px;"></div>
                    <div class="item-info">
                        <p></p>
                        <h2>{{computer}}</h2>
                    </div>
                </div>
                <div class="item">
                    <div class="item-icon" style="background-image:url('/YRCloud/resource/img/disk.png');background-size: 55px 53px;"></div>
                    <div class="item-info">
                        <p></p>
                        <h2>{{disk}}</h2>
                    </div>
                </div>
                <div class="item">
                    <div class="item-icon" style="background-image:url('/YRCloud/resource/img/snapshot.png');background-size: 55px 53px;"></div>
                    <div class="item-info">
                        <p></p>
                        <h2>{{snapshot}}</h2>
                    </div>
                </div>
            </div>
        </div>

        <div class="operStage">
            <label></label>

            <div class="block">
                <el-carousel height="350px" :interval=8000>
                    <el-carousel-item v-for="item in items">
                        <div style="display: flex;flex-wrap:wrap;justify-content:space-between;height:100%;padding: 0px 25px;">
                            <div class="flex-item" v-for="i in item">
                                <span>{{i.title}}</span>
                                <p>&nbsp;&nbsp;&nbsp;&nbsp;{{i.des}}</p>
                                <a href="#/home">&nbsp;&nbsp;&nbsp;&nbsp;></a>
                            </div>
                        </div>
                    </el-carousel-item>
                </el-carousel>
            </div>
        </div>
    </div>

</template>

<script>
    export default{
        data(){
            return {
                balance:0,
                computer:0,
                snapshot:0,
                disk:0,
                items:[
                    [{title:'',des:''},{title:'CDN',des:''},{title:'',des:''},{title:'',des:''}],
                    [{title:'VPC',des:''},{title:'',des:''},{title:'',des:''},{title:'',des:''}],
                    [{title:'',des:''},{title:' HPC',des:' GPU '},{title:'',des:'Docker'},{title:'GPU',des:'GPU'}],
                    [{title:'',des:''},{title:'',des:''},{title:'',des:''},{title:'',des:''}],
                ]
            }
        },
        created(){
            this.$http.post('user/productInfo.do').then(response => {
                if(response.ok==true&&response.status==200){
                    this.computer = response.body.computer;
                    this.snapshot = response.body.snapshot;
                    this.disk = response.body.disk;
                }
            })
            this.$http.post('device/DescribeWalletsBalance.do').then(response => {
                if(response.ok==true&&response.status==200){
                    this.balance = response.body.data.remainder;
                }
            })
        }
    }
</script>

<style scoped>
    .operStage{
        margin-top:20px;
    }
    label{
        font-size: 16px;
        padding-right: 15px;
        font-weight: 600;
    }
    .information a:first-child{
        width: 88px;
        height: 36px;
        display: inline-block;
        color: white;
        border-radius: 4px;
        background-color: #20a0ff;
        box-sizing: border-box;
        padding: 6px 19px;
        border: 1px solid #20a0ff;
    }
    .display{
        border-bottom: 2px solid #ccc;
        padding-bottom: 5px;
    }
    .item{
        width:25%;
        height: 123px;
        background-color: #f5f9fa;
        margin: 20px 50px 0px 0px;
        display: flex;
        padding: 35px 31px;
    }
    .item-icon{
        width: 58px;
        border-right:2px solid #e4ecfe;
    }
    .item-info{
        padding: 0px 19px;
    }
    .item-info p{
        font-size:19px;
    }
    .item-info h2{
        display:inline-block;
    }
    .information{
        width:32%;
        background-color: white;
        min-height:200px;
        min-width:500px;
        padding: 15px;
    }
    .flex{
        display:flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: flex-start;
        align-content: flex-start;
    }
    .flex-item{
        width:25%;
        height:100%;
        padding:63px 30px;
    }
    .flex-item p{
        line-height:50px;
    }
    .flex-item span{
        font-size: 17px;
        font-weight: 500;
    }
    .flex-item a{
        color:#20a0ff;
    }
    .el-carousel__item{
        background-color: #f5f9fa;
    }
    .block{
        margin-top:15px;
    }
    .information-wapper{
        display: block;
        font-size: 16px;
        font-weight: 400;
        padding:20px 20px 10px 20px;
    }
    .information p{
        margin-top:10px;
        border-bottom:2px solid #f5f9fa;
        font-size: 16px;
        line-height: 34px;
        margin-left: 20px;
    }
</style>




