

export default{
    dateFormat(model){
        model.forEach(item => {
            item.created = item.created.replace(/T|\+0800/g," ");
        })
        return model;
    },
    format(row, column){
        return row.caseType==1?'':
            row.caseType==2?'':'';
    },
    formatEndtime(row){
        return row.caseType==3?'- -':row.endtime;
    },
    formatCost(row){
        return row.caseType==1?row.cpCase+"/":
            row.caseType==2?row.cpCase+"/":row.cpCase+"/";
    }
}


