<template>
    <div class="wapper">
        <div class="operStage">
            <el-button type="primary" @click="createInstance"></el-button>
            <el-dropdown @command="handleCommand">
                <el-button type="primary">
                    &nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-caret-bottom el-icon--right"></i>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item :disabled="selectRow==null||selectRow.state=='Running'" command="startPrompt"></el-dropdown-item>
                    <el-dropdown-item :disabled="selectRow==null||selectRow.state=='Stopped'" command="stopPrompt"></el-dropdown-item>
                    <el-dropdown-item :disabled="selectRow==null" command="backupPrompt"></el-dropdown-item>
                    <el-dropdown-item :disabled="selectRow==null||selectRow.state=='Running'" command="updatePrompt"></el-dropdown-item>
                    <!--el-dropdown-item :disabled="selectRow==null" command="deletePrompt"></el-dropdown-item-->
                </el-dropdown-menu>
            </el-dropdown>
            <div class="table">
            <el-table
                    :data="tableData"
                    border
                    tooltip-effect="dark"
                    style="width: 100%"
                    @select-all="handleSelectAll"
                    @select="handleCurrentChange">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label=""
                        width="120"
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        prop="name"
                        label=""
                        width="120"
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        prop="state"
                        label=""
                        width="120"
                        :formatter="format">
                </el-table-column>
                <el-table-column
                        prop="templatename"
                        label=""
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        prop="serviceofferingname"
                        label=""
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        prop="created"
                        label=""
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label=""
                        width="100">
                    <template scope="scope">
                        <el-button @click="showInformation(scope.row)" type="text" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            </div>

            <el-dialog title="" v-model="dialogVisible" size="small" :modal-append-to-body=false>
                <div style="display:flex;border-top:2px solid #000">
                    <div class="config">
                        <div class="stepWapper">
                            <el-steps :active="active" finish-status="success" :align-center=true>
                                <el-step title=""></el-step>
                                <el-step title=""></el-step>
                                <el-step title=""></el-step>
                                <el-step title=""></el-step>
                            </el-steps>
                        </div>

                        <div class="confWapper" v-if="active==0">
                            <div style="width: 50px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                            <el-select v-model="zone" placeholder="">
                                <el-option
                                        v-for="item in zoneOptions"
                                        :label="item.name"
                                        :value="item.id">
                                </el-option>
                            </el-select>
                        </div>

                        <div class="confWapper" v-if="active==1">
                            <div style="width: 50px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                            <div>
                                <el-radio-group v-model="radio">
                                    <el-radio-button label="Ubuntu"></el-radio-button>
                                    <el-radio-button label="CentOS"></el-radio-button>
                                    <el-radio-button label="Windows Server"></el-radio-button>
                                    <el-radio-button label="Debian"></el-radio-button>
                                </el-radio-group>


                                <div class="OSClass" v-bind:class="{ active: item==select }" v-for="item in OS[radio]" @click="change(item)">
                                    {{item.name}}
                                </div>
                            </div>
                        </div>
                        <div>
                            <div class="confWapper flex" v-if="active==2">
                                <div style="width: 50px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;">CPU</div>
                                <div class="item-type" v-bind:class="{ active: item.cpuNum==CPUNum }" v-for="item in CPU" @click="changeCPU(item.cpuNum)">{{item.cpuNum}}</div>
                            </div>
                        </div>

                        <div class="confWapper flex" v-if="active==2&&item.cpuNum==CPUNum" v-for="item in CPU">
                            <div style="width: 50px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                            <div class="item-type" v-bind:class="{ active: ite==CPUCache }" v-for="ite in item.cache" @click="changeCache(ite)">{{ite.cache}}G</div>
                        </div>

                        <div class="confWapper flex" v-if="active==2">
                            <div style="width: 50px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                            <div style="width:70%"><el-slider v-model="disk" :step="10" show-input @change="calculationPay"></el-slider></div>
                        </div>

                        <div class="confWapper flex" v-if="active==3">
                            <div style="width: 50px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                            <el-select v-model="network" placeholder="" @change="changeNetwork">
                                <el-option
                                        v-for="item in networkOptions"
                                        :label="item.name"
                                        :value="item.ipsegmentid">
                                </el-option>
                            </el-select>
                        </div>

                        <div class="confWapper flex" v-if="active==3">
                            <div style="width: 50px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                            <div style="width:70%">
                                <el-slider v-model="bandwidth" show-input @change="calculationPay">

                                </el-slider>
                            </div>
                        </div>
                    </div>

                    <div class="display">
                        <!--div>
                            <label>OS :</label><span>{{select}}</span>
                        </div -->
                        <div>
                            <label>CPU : </label><span>{{CPUNum}}</span>
                        </div>
                        <div>
                            <label> : </label><span>{{CPUCache.cache}}G</span>
                        </div>
                        <div>
                            <label> : </label><span>{{disk}}G</span>
                        </div>
                        <div>
                            <label> : </label><span>{{networkName}}</span>
                        </div>
                        <div>
                            <label> : </label><span>{{bandwidth}}</span>
                        </div>
                        <div v-if="value=='current'">
                            <p></p>
                        </div>
                        <div v-if="value=='month'">
                            <p></p>
                        </div>
                        <div v-if="value=='year'">
                            <p></p>
                        </div>
                        <div>
                            <label> : </label>
                            <el-select v-model="value" placeholder="" class="eselect" @change="changePay">
                                <el-option
                                        v-for="item in payOptions" :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </div>
                        <div>
                            <label> : </label>
                            <el-select v-model="timeValue" :disabled="value=='current'" placeholder="" class="eselect" @change="changePay">
                                <el-option
                                        v-for="item in timeOptions" :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </div>
                        <div>
                            <label> :  </label>{{money}}<span v-if="value=='current'">/</span>

                        </div>
                    </div>
                </div>


                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="prev" v-if="active>0"></el-button>
                    <el-button type="primary" v-if="active<3" @click="next"></el-button>
                    <el-button type="primary" v-if="active>=3" :disabled="isFinish" @click="finish"></el-button>
                </span>
            </el-dialog>

            <el-dialog title="" v-model="startPrompt" size="tiny" :modal-append-to-body=false>
                <span>?</span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="startPrompt = false"> </el-button>
                    <el-button type="primary" @click="handleStart(selectRow)"> </el-button>
                </span>
            </el-dialog>
            <el-dialog title="" v-model="stopPrompt" size="tiny" :modal-append-to-body=false>
                <span>?</span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="stopPrompt = false"> </el-button>
                    <el-button type="primary" @click="handleStop(selectRow)"> </el-button>
                </span>
            </el-dialog>
            <el-dialog title="" v-model="deletePrompt" size="tiny" :modal-append-to-body=false>
                <span>?</span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="deletePrompt = false"> </el-button>
                    <el-button type="primary" @click="handleDelete(selectRow)"> </el-button>
                </span>
            </el-dialog>

            <el-dialog title="/" v-model="showInfo" size="tiny" :modal-append-to-body=false>
                <div style="border-top:2px solid rgb(0, 0, 0);">
                    <div style="padding:5px 15px;margin-top:10px"><label style="width:72px;display:inline-block;text-align:right"></label><span class="el-dialog__title">{{username}}</span></div>
                    <div style="padding:5px 15px"><label style="width:72px;display:inline-block;text-align:right"></label><span class="el-dialog__title">{{password}}</span></div>
                    <div style="padding:5px 15px"><label style="width:72px;display:inline-block;text-align:right">IP</label><span class="el-dialog__title">{{privateip}}</span></div>
                    <div style="padding:5px 15px"><label style="width:72px;display:inline-block;text-align:right">IP</label><span class="el-dialog__title">{{publicip}}</span></div>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="showInfo = false"> </el-button>
                </span>
            </el-dialog>

            <el-dialog title="" v-model="updatePrompt" size="tiny" :modal-append-to-body=false>
                <div>
                    <div class="confWapper flex" v-if="upGrade">
                        <p style="font-size:18px;color: red;"></p>
                    </div>
                    <div class="confWapper flex" v-if="!upGrade">
                        <div style="width: 50px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;">CPU</div>
                        <div class="item-type" v-bind:class="{ active: item.cpuNum==CPUNum }" v-for="item in CPU" @click="changeCPU(item.cpuNum)">{{item.cpuNum}}</div>
                    </div>
                </div>

                <div class="confWapper flex" v-if="!upGrade&&item.cpuNum==CPUNum" v-for="item in CPU">
                    <div style="width: 50px;text-align: center;font-size: 16px;line-height: 34px;font-weight: 400;"></div>
                    <div class="item-type" v-bind:class="{ active: ite==CPUCache }" v-for="ite in item.cache" @click="changeCache(ite)">{{ite.cache}}G</div>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="updatePrompt = false"> </el-button>
                    <el-button type="primary" @click="handleUpdate(selectRow)"> </el-button>
                </span>
            </el-dialog>

            <el-dialog title="" v-model="backupPrompt" size="tiny" :modal-append-to-body=false>
                <span>?</span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="backupPrompt = false"> </el-button>
                    <el-button type="primary" @click="handleBackup(selectRow)"> </el-button>
                </span>
            </el-dialog>
        </div>
    </div>

</template>

<script>
    import {Message,Loading} from 'element-ui'
    import data from './data.js'
    import util from '../util/util.js'
    import dateFormatter from '../util/dateFormatter.js'
    /*flow*/
    export default{
        data(){
            return{
                tableData:[],
                selectRow:null,
                dialogVisible: false,
                startPrompt:false,
                stopPrompt:false,
                backupPrompt:false,
                deletePrompt:false,
                updatePrompt:false,
                showInfo:false,
                username:'',
                password:'',
                privateip:'',
                publicip:'',
                promptMessage:'',
                active:0,
                radio: 'Ubuntu',
                select: 'Ubuntu Server 14.04 LTS x86_64',
                OS:{
                    Ubuntu:[],
                    CentOS:[],
                    'Windows Server':[],
                    Debian:[],
                },
                CPUNum:1,
                CPUCache:1,
                disk:40,
                CPU:[],
                payOptions:data.payOptions,
                value:'current',
                timeOptions:data.monthOptions,
                timeValue:'1',

                networkOptions:[],
                network:'',
                networkName:'',

                zoneOptions:[],
                zone:'',

                money:'0',
                bandwidth:1,

                snapshotName:'',
                snapshotDescription:'',

                upGrade:false

            }
        },
        created(){
            this.$http.get('information/listVirtualMachines.do').then(response=>{
                if(response.ok==true&&response.status==200){
                    this.tableData = dateFormatter.dateFormat(response.body.listvirtualmachinesresponse.virtualmachine);
                }
            })
        },
        methods:{
            createInstance(){
                this.dialogVisible=true;

                this.OS = {
                    Ubuntu:[],
                    CentOS:[],
                    'Windows Server':[],
                    Debian:[],
                }
                this.active = 0;
                this.$http.get('information/listTemplates.do').then(
                    response =>{

                        if(response.ok==true&&response.status==200){
                            let result = response.body.listtemplatesresponse;
                            for(let z in result.template){
                                if(result.template[z].name.indexOf("CentOS")>=0){
                                    this.OS.CentOS.push(result.template[z])
                                }
                                if(result.template[z].name.indexOf("Ubuntu")>=0){
                                    this.OS.Ubuntu.push(result.template[z])
                                }
                                if(result.template[z].name.indexOf("Debian")>=0){
                                    this.OS.Debian.push(result.template[z])
                                }
                            }
                            this.select = result.template[0];
                            console.log(this.select)
                        }
                    }
                )
                this.$http.get('information/zone.do').then(
                    response =>{
                        if(response.ok==true&&response.status==200){
                            this.zoneOptions = response.body.listzonesresponse.zone;
                            this.zone = response.body.listzonesresponse.zone[0].id;
                        }
                    }
                )
                var attrOptions = {
                    networkOptions:'result'
                }
                util.get('network/listNetDefault.do',this,attrOptions);
                this.$http.get('information/listServiceOfferings.do').then(
                    response =>{
                        console.log(response);
                        if(response.ok==true&&response.status==200){
                            this.CPU = response.body;
                            this.CPUNum = response.body[0].cpuNum;
                            this.CPUCache = response.body[0].cache[0];
                        }
                    }
                )

                //const result = {"listtemplatesresponse":{"count":1,"template":[{"id":"be62d248-19c1-11e7-aac7-005056ac4aa8","name":"CentOS 5.6 (64-bit) no GUI (Simulator)","displaytext":"CentOS 5.6 (64-bit) no GUI (Simulator)","ispublic":true,"created":"2017-04-05T21:35:57+0800","isready":true,"passwordenabled":false,"format":"VHD","isfeatured":true,"crossZones":true,"ostypeid":"6cf5aeb2-19c1-11e7-aac7-005056ac4aa8","ostypename":"CentOS 5.6 (64-bit)","account":"system","zoneid":"7b899cb6-b128-4328-a820-2f765d7d74ad","zonename":"Sandbox-simulator","status":"Download Complete","size":2147483648,"templatetype":"BUILTIN","hypervisor":"Simulator","domain":"ROOT","domainid":"6d0fa696-19c1-11e7-aac7-005056ac4aa8","isextractable":false,"checksum":"","sshkeyenabled":false,"isdynamicallyscalable":false,"tags":[]}]}}
                //result.listtemplatesresponse

            },
            handleCurrentChange(select,row){
                if(select.length>1){
                    select.splice(0,1);
                }
                this.selectRow = row;
                if(select.length==0){
                    this.selectRow = null;
                }
            },
            handleSelectAll(select){
                select.splice(0,select.length);
                this.selectRow = null;
            },
            prev(){
                this.active--;
            },
            next(){
                this.active++;
                util.calculationPay(this);
            },
            change(select){

                this.select = select;
            },
            changeCPU(CPUNum){
                this.CPUNum = CPUNum;
                for(let item in this.CPU){
                    if(this.CPU[item].cpuNum==CPUNum){
                        this.CPUCache = this.CPU[item].cache[0];
                    }
                }
                var url = 'device/QueryBillingPrice.do';
                var params = {};
                params.cpunum = this.CPUNum;
                params.memory = this.CPUCache.cache;
                params.disk = this.disk;
                params.value = this.value;
                params.timevalue = this.timeValue;
                params.bandwidth = this.bandwidth;
                var attrOptions = {
                    money:'cost'
                }
                util.post(url,params,this,attrOptions)
            },
            changeCache(CPUCache){
                this.CPUCache = CPUCache;
                var url = 'device/QueryBillingPrice.do';
                var params = {};
                params.cpunum = this.CPUNum;
                params.memory = this.CPUCache.cache;
                params.disk = this.disk;
                params.value = this.value;
                params.timevalue = this.timeValue;
                params.bandwidth = this.bandwidth;
                var attrOptions = {
                    money:'cost'
                }
                util.post(url,params,this,attrOptions)
            },
            changePay(value){
                ///
                util.processTimeChange(value,this,data);
                //
                var url = 'device/QueryBillingPrice.do';
                let params = {};
                params.cpunum = this.CPUNum;
                params.memory = this.CPUCache.cache;
                params.disk = this.disk;
                params.value = this.value;
                params.timevalue = this.timeValue;
                params.bandwidth = this.bandwidth;
                var attrOptions = {
                    money:'cost'
                }
                util.post(url,params,this,attrOptions)
            },
            finish(){
                this.dialogVisible = false;
                let loadingInstance = Loading.service({
                    text:'...'
                })
                let params = "?";
                params += "zoneid="+this.zone;
                params += "&templateid="+this.select.id;
                params += "&serviceofferingid="+this.CPUCache.id;
                params += "&diskofferingid=fcafa58a-573c-4606-b729-c65bf041b004&size="+this.disk;
                params += "&networkid="+this.network;
                params += "&value="+this.value;
                params += "&timevalue="+this.timeValue;
                params += "&bandwidth="+this.bandwidth;
                this.$http.get('information/deployVirtualMachine.do'+params).then(response => {
                    if(response.ok==true&&response.status==200){
                        if(response.body.status==1){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'success'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvirtualmachinesresponse.virtualmachine);
                        }else if(response.body.status==2){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvirtualmachinesresponse.virtualmachine);
                        }else{
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                    }else{
                        Message({
                            message:'',
                            type:'error'
                        })
                    }
                }, response => {
                    loadingInstance.close();
                    Message({
                        message:'',
                        type:'error'
                    })
                })
            },
            changeNetwork(networkId){
                for(let item in this.networkOptions){
                    if(this.networkOptions[item].id == networkId){
                        this.networkName = this.networkOptions[item].name;
                        break;
                    }
                }
            },
            handleEdit(index,item){
                console.log(index);
                console.log(item);
            },
            calculationPay(){
                util.calculationPay(this);
            },
            handleCommand(type){
                console.log(type);
                if(type=='updatePrompt'){
                    this.$http.get('information/upgrade.do?id='+this.selectRow.id).then(
                        response =>{
                            if(response.ok==true&&response.status==200){
                                if(response.body.upgrade==1){
                                    this.upGrade = false;
                                }else{
                                    this.upGrade = true;
                                }
                            }
                        }
                    )
                    this.$http.get('information/listServiceOfferings.do').then(
                        response =>{
                            console.log(response);
                            if(response.ok==true&&response.status==200){
                                this.CPU = response.body;
                                this.CPUNum = response.body[0].cpuNum;
                                this.CPUCache = response.body[0].cache[0];
                            }
                        }
                    )
                }
                this[type] = true;
            },
            handleStart(row){
                this.startPrompt = false;
                let loadingInstance = Loading.service({
                    text:'...'
                })
                this.$http.post('information/startVirtualMachine.do',{
                    virtualMachineid:row.id,
                }).then(response => {
                    if(response.ok==true&&response.status==200){
                        console.log(response.body);
                        if(response.body.status==1){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'success'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvirtualmachinesresponse.virtualmachine);
                        }else if(response.body.status==2){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvirtualmachinesresponse.virtualmachine);
                        }else{
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                        //util.queryStartJobResult(this,response.body.startvirtualmachineresponse.jobid,loadingInstance);
                    }else{
                        loadingInstance.close();
                        Message({
                            message:'',
                            type:'error'
                        });
                    }
                }, response => {
                    loadingInstance.close();
                    Message({
                        message:'',
                        type:'error'
                    })
                })
            },
            handleStop(row){
                this.stopPrompt = false;
                let loadingInstance = Loading.service({
                    text:'...'
                })
                this.$http.post('information/stopVirtualMachine.do',{
                    virtualMachineid:row.id,
                    forced:true
                }).then(response => {
                    if(response.ok==true&&response.status==200){
                        if(response.body.status==1){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'success'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvirtualmachinesresponse.virtualmachine);
                        }else if(response.body.status==2){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvirtualmachinesresponse.virtualmachine);
                        }else{
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                        //util.queryStartJobResult(this,response.body.startvirtualmachineresponse.jobid,loadingInstance);
                    }else{
                        loadingInstance.close();
                        Message({
                            message:'',
                            type:'error'
                        });
                    }
                }, response => {
                    loadingInstance.close();
                    Message({
                        message:'',
                        type:'error'
                    })
                })
            },
            handleBackup(row){
                this.backupPrompt = false;
                let loadingInstance = Loading.service({
                    text:'...'
                });
                this.$http.get('Snapshot/createVMSnapshot.do?virtualmachineid='+row.id+'&name='+this.snapshotName+'&description='+this.snapshotDescription).then(response => {
                    loadingInstance.close();
                    if(response.ok==true&&response.status==200){
                        if(response.body.status==1){
                            Message({
                                message:'',
                                type:'success'
                            });
                            console.log(response.body);
                        }else if(response.body.status==2){
                            Message({
                                message:'',
                                type:'error'
                            });
                        }else{
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                    }else{
                        Message({
                            message:'',
                            type:'error'
                        });
                    }
                },response => {
                    loadingInstance.close();
                    Message({
                        message:'',
                        type:'error'
                    });
                })
            },
            handleDelete(row){
                this.deletePrompt = false;
                let loadingInstance = Loading.service({
                    text:'...'
                })
                this.$http.post('information/destroyVirtualMachine.do',{
                    virtualMachineid:row.id
                }).then(response => {
                    if(response.ok==true&&response.status==200){
                        if(response.body.status==1){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'success'
                            });
                            if(response.body.listvirtualmachinesresponse.hasOwnProperty("virtualmachine")){
                                this.tableData = dateFormatter.dateFormat(response.body.listvirtualmachinesresponse.virtualmachine);
                            }else{
                                this.tableData = [];
                            }
                        }else if(response.body.status==2){
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                            this.tableData = dateFormatter.dateFormat(response.body.listvirtualmachinesresponse.virtualmachine);
                        }else{
                            loadingInstance.close();
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                        //util.queryStartJobResult(this,response.body.startvirtualmachineresponse.jobid,loadingInstance);
                    }else{
                        loadingInstance.close();
                        Message({
                            message:'',
                            type:'error'
                        });
                    }
                }, response => {
                    loadingInstance.close();
                    Message({
                        message:'',
                        type:'error'
                    })
                })
            },
            handleUpdate(row){
                let loadingInstance = Loading.service({
                    text:'...'
                })
                this.updatePrompt = false;
                console.log(row);
                this.$http.get('information/scaleVirtualMachine.do?virtualMachineid='+row.id+'&serviceofferingid='+this.CPUCache.id).then(response => {
                    loadingInstance.close();
                    if(response.ok==true&&response.status==200){
                        if(response.body.status==1){
                            Message({
                                message:'',
                                type:'success'
                            })
                            this.tableData = dateFormatter.dateFormat(response.body.listvirtualmachinesresponse.virtualmachine);
                        }else{
                            Message({
                                message:'',
                                type:'error'
                            })
                        }
                    }
                },response => {
                    loadingInstance.close();
                    Message({
                        message:'',
                        type:'error'
                    })
                })
            },
            format(row){
                return row.state=='Running'?'':'';
            },
            showInformation(row){
                this.$http.get('information/showInfo.do?vmid='+row.id).then(
                    response => {
                        if(response.ok==true&&response.status==200){
                            this.username = response.body.username;
                            this.password = response.body.password;
                            this.privateip = response.body.privateip;
                            this.publicip = response.body.publicip;
                            this.showInfo = true;
                        }else{
                            Message({
                                message:'',
                                type:'error'
                            });
                        }
                    },
                    response => {
                        Message({
                            message:'',
                            type:'error'
                        });
                    }
                )
                console.log(row);
            }
        },
        computed:{
            isFinish:function(){
                return !(this.zone&&this.network);
            }
        }
    }
</script>

<style scoped>
    .item i{
        font-size: 18px;
        width: 7px;
        display: inline-block;
        color: red;
    }
    .table{
        padding-top:15px;
    }
    .stepWapper{
        padding:10px 50px;
    }
    .confWapper{
        display:flex;
        padding: 10px 37px;
    }
    .OSClass{
        padding: 5px 15px;
        margin: 5px 0px;
        border: 1px solid #ccc;
        border-radius: 3px;
        width: 382px;
        cursor:pointer;
    }
    .flex{
        display: flex;
    }
    .active{
        background-color: #20a0ff;;
    }
    .config{
        width:70%;
        margin-top:10px;
    }
    .display{
        background-color: #eaeaea;
        width:30%;
    }
    .item-type{
        margin-right: 10px;
        padding: 10px 25px;
        text-align: center;
        border-radius: 4px;
        border: 1px solid #ccc;
        cursor: pointer;
    }
    .display span{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .display div{
        border-bottom:1px solid #dddddd;
    }
    .display label{
        display: inline-block;
        width: 70px;
        height: 50px;
        text-align: right;
        line-height: 50px;
        font-size: 14px;
    }
    .display p{
        padding: 10px 15px;
        color: red;
    }
    .eselect{
        width: 165px;
    }
    .el-dialog__header{
        border-bottom:2px solid black;
    }
</style>


