<template>
    <div>
        <el-menu default-active="2" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" theme="dark" :unique-opened=true :router=true>
            <el-menu-item index="home"><i class="el-icon-menu"></i></el-menu-item>
            <el-submenu index="2">
                <template slot="title"></template>
                    <el-menu-item index="host"></el-menu-item>
                    <el-menu-item index="disk"></el-menu-item>
                    <el-menu-item index="snapshot"></el-menu-item>
            </el-submenu>
            <el-submenu index="3">
                <template slot="title"></template>
                <el-menu-item index="VPC">VPC</el-menu-item>
                <el-menu-item index="subnetwork"></el-menu-item>
