                <el-menu-item index="feeest"></el-menu-item>
            </el-submenu>
            <el-menu-item index="log"></el-menu-item>
        </el-menu>

    </div>

</template>

<script>

    /*flow*/
    export default{
        data(arg:string):object{
            return{}
        },
        created(){

        },
        methods: {
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            },
            handleSelect(key){
                console.log(key);
            }
        }
    }
</script>

<style scoped>
    div{
        height:100%;
        position:fixed;
        display:inline-block;
        width:12%;
    }
    ul{
        height:100%;
    }
</style>


